* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::after {
    box-sizing: border-box;
}

::before {
    box-sizing: border-box;
}

#html {
    word-break: break-all;
}

#div1 {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 20px;
}

#div1::before {
    content: ' ';
    display: block;
    width: 100px;
    height: 100px;
    position: absolute;

}

#div1::after {
    content: ' ';
    content: ' ';
    display: block;
    width: 100px;
    height: 100px;
    position: absolute;
}

@media (max-width:500px) {
    #html {
        height: 50vh;
        overflow: auto;
    }

    #div1 {
        position: relative;
        top: 0;
        right: 0;
    }

    #div1Wrapper {
        height: 50vh;

    }
}